<template>
  <div class="list-group">
    <router-link
      v-for="friend in friends"
      :key="friend.id"
      :to="{name: 'friend', params: {'friend': friend}}"
      class="list-group-item list-group-item-action item"
    >
      <img class="icon navbar-brand" :src="friend.avatar" />
      <span class="item-text">{{ friend.nickname }}</span>
    </router-link>
  </div>
</template>

<script>
import request from "../libs/http";

export default {
  name: "Friends",

  data: function() {
    return {
      friends: []
    };
  },
  created: function() {
    request.get("/api/social/friend_list").then(response => {
      if (response.data.code === 0) {
        this.friends = response.data.data;
      }
    });
  }
};
</script>

