<template>
  <div class="container-fluid box">
    <div class="form-horizontal">
      <h5>上传您的个人形象</h5>
      <hr />

      <div class="form-group bottom-holder">
        <input @change="handle_file($event.target.files)" type="file" />
      </div>

      <button class="btn btn-primary col-12" v-on:click="upload()">点击提交</button>
    </div>
  </div>
</template>

<script>
import request from "../libs/http";

export default {
  name: "UploadAvatar",

  data() {
    return {
      file: undefined
    };
  },

  methods: {
    // Handles a change on the file upload
    handle_file(files) {
      this.file = files[0];
    },

    // Submits the file to the server
    upload() {
      // Initialize the form data
      let formData = new FormData();

      // Add the form data we need to submit
      formData.append("avatar", this.file);

      // Make the request to the POST /single-file URL
      request
        .post("/api/user/upload_avatar", formData, {
          headers: { "Content-Type": "multipart/form-data" }
        })
        .then(response => {
          console.log("上传成功");
          this.$store.state.self.avatar = response.data.data.avatar_url;
        });
      this.$router.push("/user/show");
    }
  }
};
</script>

