<template>
  <div class="list-group">
    <router-link
      v-for="user in users"
      :key="user.id"
      :to="{name: 'stanger', params: {'stanger': user}}"
      class="list-group-item list-group-item-action item"
    >
      <span class="rank-txt">{{ user.rank }}.</span>
      <img class="icon navbar-brand" :src="user.avatar" />
      <span class="item-txt">{{ user.nickname }} / {{ user.score }} 分</span>
    </router-link>
  </div>
</template>

<script>
import request from "../libs/http";

export default {
  name: "Friends",

  data: function() {
    return {
      users: []
    };
  },
  created: function() {
    request.get("/api/social/hot_rank").then(response => {
      if (response.data.code === 0) {
        this.users = response.data.data;
      }
    });
  }
};
</script>

