class User {
  constructor(fields) {
    this.id = fields.id;
    this.nickname = fields.nickname;
    this.phonenum = fields.phonenum;
    this.gender = fields.gender;
    this.birthday = fields.birthday;
    this.location = fields.location;
    this.avatar = fields.avatar;

    // 附加属性
    this.profile = new Profile({});
  }

  is_self(user) {
    return this.id === user.id;
  }

  set_profile(profile) {
    if (profile instanceof Profile) {
      this.profile = profile;
    } else {
      throw "Type Error";
    }
  }

  to_dict() {
    let dict = {};
    for (let k in this) {
      dict[k] = this[k];
    }
    return dict;
  }

  copy() {
    let user = new User(this.to_dict());
    let profile = new Profile(this.profile.to_dict());
    user.set_profile(profile);
    return user;
  }
}

class Profile {
  constructor(fields) {
    this.dating_gender = fields.dating_gender;
    this.dating_location = fields.dating_location;
    this.min_distance = fields.min_distance;
    this.max_distance = fields.max_distance;
    this.min_dating_age = fields.min_dating_age;
    this.max_dating_age = fields.max_dating_age;
    this.vibration = fields.vibration;
    this.only_matche = fields.only_matche;
    this.auto_play = fields.auto_play;
  }

  to_dict() {
    let dict = {};
    for (let k in this) {
      dict[k] = this[k];
    }
    return dict;
  }
}

export { User, Profile };
