import Vue from "vue";
import Vuex from "vuex";
import { User } from "./models";

Vue.use(Vuex);

const store = new Vuex.Store({
  state: {
    self: undefined,
    queue: []
  },

  mutations: {
    set_self: function(state, user) {
      if (user instanceof User) {
        state.self = user;
      } else {
        throw "Type Error";
      }
    },

    reset_self: function(state) {
      state.self = undefined;
      state.queue = [];
    },

    set_profile: function(state, profile) {
      state.self.set_profile(profile);
    }
  }
});

export default store;
