<template>
  <div class="container-fluid box">
    <router-link to="/upload/avatar">
      <img v-if="self.avatar" class="img-thumbnail avatar" :src="self.avatar" />
      <img v-else class="img-thumbnail avatar" src="../assets/img/default.jpg" />
    </router-link>

    <div class="info">
      <h5>个人资料</h5>
      <table class="table">
        <tbody>
          <tr>
            <th width="35%">昵称</th>
            <td>{{ self.nickname }}</td>
          </tr>
          <tr>
            <th>手机</th>
            <td>{{ self.phonenum }}</td>
          </tr>
          <tr>
            <th>性别</th>
            <td>{{ self.gender == "male" ? "男" : "女" }}</td>
          </tr>
          <tr>
            <th>生日</th>
            <td>{{ self.birthday }}</td>
          </tr>
          <tr>
            <th>地区</th>
            <td>{{ self.location }}</td>
          </tr>
        </tbody>
      </table>
    </div>

    <div v-if="profile_btn">
      <button @click="show_more()" class="btn btn-success col-10">查看更多</button>
    </div>

    <div v-else class="info">
      <h5>交友资料</h5>
      <table class="table">
        <tbody>
          <tr>
            <th width="35%">交友性别</th>
            <td>{{ self.profile.dating_gender == "male" ? "小哥哥" : "小姐姐" }}</td>
          </tr>
          <tr>
            <th>交友城市</th>
            <td>{{ self.profile.dating_location }}</td>
          </tr>
          <tr>
            <th>最大年龄</th>
            <td>{{ self.profile.max_dating_age }} 岁</td>
          </tr>
          <tr>
            <th>最小年龄</th>
            <td>{{ self.profile.min_dating_age }} 岁</td>
          </tr>
          <tr>
            <th>最大距离</th>
            <td>{{ self.profile.max_distance }} 公里</td>
          </tr>
          <tr>
            <th>最小距离</th>
            <td>{{ self.profile.min_distance }} 公里</td>
          </tr>
          <tr>
            <th>自动播放</th>
            <td>{{ self.profile.auto_play ? "是" : "否" }}</td>
          </tr>
          <tr></tr>
          <tr>
            <th>资料保密</th>
            <td>{{ self.profile.only_matche ? "是" : "否" }}</td>
          </tr>
          <tr>
            <th>开启震动</th>
            <td>{{ self.profile.vibration ? "是" : "否" }}</td>
          </tr>
        </tbody>
      </table>
    </div>
    <br />
    <router-link v-if="!profile_btn" to="/user/update" class="btn btn-primary col-10">修改资料</router-link>
  </div>
</template>

<script>
import request from "../libs/http";
import { Profile } from "../libs/models";

export default {
  name: "UserShow",
  props: ["self"],

  data: function() {
    return {
      profile_btn: true
    };
  },

  methods: {
    show_more() {
      if (this.self.profile.dating_gender === undefined) {
        console.log("从服务器获取 profile");
        request.get("/api/user/get_profile").then((response) => {
          let profile = new Profile(response.data.data);
          this.self.set_profile(profile);
        });
      }
      this.profile_btn = false;
    }
  }
};
</script>

