import Vue from "vue";
import VueRouter from "vue-router";

Vue.use(VueRouter);

import store from "./storage";
import Login from "../components/Login";
import Menu from "../components/Menu";
import UserShow from "../components/UserShow";
import UserUpdate from "../components/UserUpdate";
import UploadAvatar from "../components/UploadAvatar";
import Swiper from "../components/Swiper";
import StangerShow from "../components/StangerShow";
import Friends from "../components/Friends";
import FriendShow from "../components/FriendShow";
import Fans from "../components/Fans";
import HotRank from "../components/HotRank";

let router = new VueRouter({
  routes: [
    { name: "home", path: "/", component: Login, props: true, meta: { requiresAuth: false } },
    { name: "login", path: "/login", component: Login, props: true, meta: { requiresAuth: false } },
    { name: "menu", path: "/menu", component: Menu, props: true, meta: { requiresAuth: false } },
    { name: "show", path: "/user/show", component: UserShow, props: true, meta: { requiresAuth: true } },
    { name: "update", path: "/user/update", component: UserUpdate, props: true, meta: { requiresAuth: true } },
    { name: "avatar", path: "/upload/avatar", component: UploadAvatar, props: true, meta: { requiresAuth: true } },

    { name: "swiper", path: "/swiper", component: Swiper, props: true, meta: { requiresAuth: true } },
    { name: "stanger", path: "/stanger/show", component: StangerShow, props: true, meta: { requiresAuth: true } },
    { name: "friends", path: "/friends", component: Friends, props: true, meta: { requiresAuth: true } },
    { name: "friend", path: "/friend/show", component: FriendShow, props: true, meta: { requiresAuth: true } },
    { name: "fans", path: "/fans", component: Fans, props: true, meta: { requiresAuth: true } },
    { name: "rank", path: "/hot/rank", component: HotRank, props: true, meta: { requiresAuth: true } }
  ]
});

router.beforeEach((to, _, next) => {
  if (to.matched.some((record) => record.meta.requiresAuth)) {
    if (store.state.self == undefined) {
      console.log(`尚未登录: ${to.path} => /login`);
      next({ path: "/login" });
    } else {
      console.log(`${store.state.self.nickname} => ${to.path}`);
      next();
    }
  } else {
    console.log(`无需登录 => ${to.path}`);
    next();
  }
});

export default router;
