  <div class="container-fluid box">
    <form class="form-horizontal">
      <router-link to="/upload/avatar">
        <img v-if="user.avatar" class="img-thumbnail avatar" :src="user.avatar" />
        <img v-else class="img-thumbnail avatar" src="../assets/img/default.jpg" />
      </router-link>

      <div class="info">
        <h5>基本资料</h5>

        <div class="form-group row">
          <label class="col-sm-3 col-form-label lable">昵称</label>
          <div class="inputer">
            <input v-model="user.nickname" class="form-control" />
          </div>
        </div>

        <div class="form-group row">
          <label class="col-sm-3 col-form-label lable">手机</label>
          <div class="inputer">
            <input v-model="user.phonenum" class="form-control" />
          </div>
        </div>

        <fieldset class="form-group">
          <div class="row">
            <legend class="col-form-label col-sm pt-0 lable">性别</legend>
            <div class="form-check radio-edge">
              <input v-model="user.gender" type="radio" value="male" class="form-check-input" />
              <label class="form-check-label">小哥哥</label>
            </div>

            <div class="form-check">
              <input v-model="user.gender" type="radio" value="female" class="form-check-input" />
              <label class="form-check-label">小姐姐</label>
            </div>
          </div>
        </fieldset>

        <div class="form-group row">
          <label class="col-sm-3 col-form-label lable">生日</label>
          <div class="inputer">
            <input v-model="user.birthday" type="date" class="form-control" min="1900-01-01" max="2004-12-31" />
          </div>
        </div>

        <div class="form-group row">
          <label class="col-sm-3 col-form-label lable">地区</label>
          <select v-model="user.location" class="form-control form-control-sm inputer">
            <option v-for="ct in citys" :value="ct" :key="ct">{{ ct }}</option>
          </select>
        </div>
      </div>

      <div class="info">
        <h5>其他设置</h5>

        <fieldset class="form-group">
          <div class="row">
            <legend class="col-form-label col-sm pt-0 lable">交友性别</legend>
            <div class="form-check radio-edge">
              <input v-model="user.profile.dating_gender" type="radio" value="male" class="form-check-input" />
              <label class="form-check-label">小哥哥</label>
            </div>

            <div class="form-check">
              <input v-model="user.profile.dating_gender" type="radio" value="female" class="form-check-input" />
              <label class="form-check-label">小姐姐</label>
            </div>
          </div>
        </fieldset>

        <div class="form-group row">
          <label class="col-sm-3 col-form-label lable">交友城市</label>
          <select v-model="user.profile.dating_location" class="form-control form-control-sm inputer">
            <option v-for="ct in citys" :value="ct" :key="ct">{{ ct }}</option>
          </select>
        </div>

        <div class="form-group row">
          <label class="col-sm-3 col-form-label lable">最大年龄</label>
          <select v-model.number="user.profile.max_dating_age" class="form-control form-control-sm inputer">
            <option v-for="age in ages" :value="age" :key="age">{{ age }}</option>
          </select>
        </div>

        <div class="form-group row">
          <label class="col-sm-3 col-form-label lable">最小年龄</label>
          <select v-model.number="user.profile.min_dating_age" class="form-control form-control-sm inputer">
            <option v-for="age in ages" :value="age" :key="age">{{ age }}</option>
          </select>
        </div>

        <div class="form-group row">
          <label class="col-sm-3 col-form-label lable">最大距离</label>
          <select v-model.number="user.profile.max_distance" class="form-control form-control-sm inputer">
            <option v-for="distance in range" :value="distance" :key="distance">{{ distance }}</option>
          </select>
        </div>

        <div class="form-group row">
          <label class="col-sm-3 col-form-label lable">最小距离</label>
          <select v-model.number="user.profile.min_distance" class="form-control form-control-sm inputer">
            <option v-for="distance in range" :value="distance" :key="distance">{{ distance }}</option>
          </select>
        </div>

        <fieldset class="form-group">
          <div class="row">
            <legend class="col-form-label col-sm pt-0 lable">自动播放</legend>
            <div class="form-check radio-edge">
              <input v-model="user.profile.auto_play" type="radio" value="true" class="form-check-input" />
              <label class="form-check-label">开</label>
            </div>

            <div class="form-check">
              <input v-model="user.profile.auto_play" type="radio" value="false" class="form-check-input" />
              <label class="form-check-label">关</label>
            </div>
          </div>
        </fieldset>

        <fieldset class="form-group">
          <div class="row">
            <legend class="col-form-label col-sm pt-0 lable">资料保密</legend>
            <div class="form-check radio-edge">
              <input v-model="user.profile.only_matche" type="radio" value="true" class="form-check-input" />
              <label class="form-check-label">开</label>
            </div>

            <div class="form-check">
              <input v-model="user.profile.only_matche" type="radio" value="false" class="form-check-input" />
              <label class="form-check-label">关</label>
            </div>
          </div>
        </fieldset>

        <fieldset class="form-group">
          <div class="row">
            <legend class="col-form-label col-sm pt-0 lable">开启震动</legend>
            <div class="form-check radio-edge">
              <input v-model="user.profile.vibration" type="radio" value="true" class="form-check-input" />
              <label class="form-check-label">开</label>
            </div>

            <div class="form-check">
              <input v-model="user.profile.vibration" type="radio" value="false" class="form-check-input" />
              <label class="form-check-label">关</label>
            </div>
          </div>
        </fieldset>
      </div>
    </form>

    <button @click="submit()" class="btn btn-primary col-10">保存修改</button>
  </div>
</template>

<script>
import request from "../libs/http";
export default {
  name: "UserUpdate",
  props: ["self"],

  data: function() {
    return {
      user: this.self.copy(),
      citys: ["北京", "上海", "深圳", "武汉", "成都", "西安", "沈阳"],
      ages: [...Array(43).keys()].map((i) => i + 18),
      range: [...Array(10).keys()].map((i) => i + 1)
    };
  },

  methods: {
    submit: function() {
      // 布尔类型强转一下
      this.user.profile.auto_play = eval(this.user.profile.auto_play);
      this.user.profile.only_matche = eval(this.user.profile.only_matche);
      this.user.profile.vibration = eval(this.user.profile.vibration);

      // 构造参数
      let params = this.user.to_dict();
      let p_dict = this.user.profile.to_dict();
      for (let k in p_dict) {
        params[k] = p_dict[k];
      }

      request.post("/api/user/set_profile", params).then((response) => {
        if (response.data.code === 0) {
          this.$store.state.self = this.user;
          this.$router.push("/user/show");
        }
      });
    }
  }
};
</script>

